#logo {
    background: linear-gradient(yellow, red);
    -webkit-background-clip: text;
    color: transparent;
}

#navbarAllproducts {
    background-color: #2b2d42;
}

#botonesLink {
    text-decoration: none;
    margin: 5px;
    color: black;

}

#menuNavbar {
    display: flex;
    flex-direction: column;
    background-color: white;
}


#botonesStyle {
    background-color: #f77f00;
    color: white;
    width: 100%;
}

#botonStyleDisable {
    background-color: white;
    color: #3a354d;
    font-weight: bold;
    border-radius: 0;
    width: 100%;
    margin-top: 3px;
    box-shadow: none;
}

#textoNavbar {
    background-color: black;
    color: white;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
    animation: cambiar 15s infinite;
    font-weight: bold;
}

@keyframes cambiar {
    0% {
        margin-right: -150%
    }

    40% {
        margin-right: 0
    }

    50% {
        margin-right: 0
    }

    60% {
        margin-right: 0
    }

    100% {
        margin-right: 150%
    }
}

@media (max-width: 1350px) {

    #textoNavbar {
        font-size: 14px;
    }

    @keyframes cambiar {
        0% {
            margin-right: -150%
        }

        40% {
            margin-right: 0
        }

        50% {
            margin-right: 0
        }

        60% {
            margin-right: 0
        }

        100% {
            margin-right: 150%
        }
    }

}

@media (max-width: 960px) {
    #containerNavbar {
        position: fixed;
        width: 100%;
        top: 0;
        z-index: 1;
    }

    #textoNavbar {
        font-size: 11px;
    }

    @keyframes cambiar {
        0% {
            margin-right: -300%
        }

        40% {
            margin-right: 0
        }

        50% {
            margin-right: 0
        }

        60% {
            margin-right: 0
        }

        100% {
            margin-right: 150%
        }
    }

}