#itemListContainer {
    display: grid;
    height: 100%;
    grid-template-columns: repeat(auto-fit,
            minmax(345px, 1fr));
    gap: 50px;
    background-color: rgba(255, 255, 255, 0.5);
}

#items {
    height: auto;
    width: 345px;
    margin: 15% 10%;
    border-radius: 5px;
}

#imagenProducto {
    border: 1px solid white;
}

#imagenProducto:hover {
    transform: scale(1.10);
    border: 1px solid white;
    transition: all 0.5s ease;
}

#divButton {
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70px;
    background-color: #2b2d42;
}

#button {
    color: white;
    background-color: #f77f00;
    border: solid #f77f00;
    border-radius: 5px;
}

#button:hover {
    background-color: white;
    color: #f77f00;
    border: solid #f77f00;
    border-radius: 5px;
}


@media (max-width: 960px) {
    #itemListContainer {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    #items {
        width: 345px;
        height: auto;
        margin: 15%;
    }

}